import buildAppReducer from './appreducer'
import React, { createContext, useReducer } from 'react'
import AccountService from '../account/accountservice'
import buildFetchTransactions from '../transaction/fetchtransactions'

const initialState = {
  user: null,
  token: null,
  transactions: [],
  error: null,
  eventSource: null,
  fetchTransactions: () => {},
}

export const GlobalProvider = ({ apiBaseUri, children }) => {
  const { login, googleLogin } = AccountService({ apiBaseUri })

  const AppReducer = buildAppReducer({ apiBaseUri })
  const getTransactions = buildFetchTransactions({ apiBaseUri })

  const [state, dispatch] = useReducer(AppReducer, initialState)

  const fetchTransactions = async ({ token }) => {
    const transactions = await getTransactions({ token })

    dispatch({ type: 'FETCH_TRANSACTIONS', payload: transactions })
  }
  const dispatchLogin = (user) => {
    dispatch({ type: 'LOGIN', payload: { user: user.user } })

    const { token } = user.user
    fetchTransactions({ token })
  }
  const doLogin = async (username, password) => {
    try {
      const user = await login(username, password)

      dispatchLogin(user)
    } catch (error) {
      dispatch({ type: 'LOGIN', payload: { error: error.message } })
    }
  }
  const doGoogleLogin = async (profile) => {
    try {
      const user = await googleLogin({
        token: profile.credential,
        username: 'Jamie',
      })

      dispatchLogin(user)
    } catch (error) {
      dispatch({ type: 'LOGIN', payload: { error: error.message } })
    }
  }

  const doLogout = () => {
    dispatch({ type: 'LOGOUT' })
  }

  const doAddTransaction = ({ transaction, token }) => {
    fetch(apiBaseUri + '/transaction/', {
      method: 'POST',
      mode: 'cors',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(transaction),
    })
      .then(async (response) => {
        if (response.ok) {
          const { transaction } = await response.json()

          dispatch({ type: 'ADD_TRANSACTION', payload: transaction })
          return transaction
        } else {
          dispatch({
            type: 'ADD_TRANSACTION',
            payload: { error: response.statusText },
          })
        }

        return
      })
      .catch((error) => {
        dispatch({ type: 'ADD_TRANSACTION', payload: { error: error.message } })
      })
  }
  return (
    <GlobalContext.Provider
      value={{
        transactions: state.transactions,
        user: state.user,
        error: state.error,
        eventSource: state.eventSource,
        fetchTransactions,
        doLogin,
        doGoogleLogin,
        doLogout,
        doAddTransaction,
      }}
    >
      {children}
    </GlobalContext.Provider>
  )
}

export const GlobalContext = createContext()
