import { useContext, useEffect, useState } from 'react'
import { Container, Paper, Card, Box } from '@material-ui/core'
import { TransactionSummary, BalanceSummary } from '../transaction/'
import AddTransaction from '../transaction/addtransaction'
import { GlobalContext } from '../context/global'
import { Redirect } from 'react-router-dom'
import { useEvents } from '../hooks/useevents'

export default ({ apiBaseUri }) => {
  const {
    transactions: txns,
    user,
    doAddTransaction,
    fetchTransactions,
  } = useContext(GlobalContext)

  let fullTransactionList = []

  if (user) {
    const uri = `${apiBaseUri}/events/?token=${user.token}`
    const eventName = 'transactionPosted'

    const transactions = useEvents({ uri, eventName })
    fullTransactionList = transactions.concat(txns)
  }
  //fetchTransactions({ token: user.token })

  return user ? (
    <Container>
      <Box display="grid" margin="auto">
        <BalanceSummary transactions={fullTransactionList} user={user} />
        <TransactionSummary transactions={fullTransactionList} />
        <AddTransaction addTransaction={doAddTransaction} />
      </Box>
    </Container>
  ) : (
    <Redirect to="/login" />
  )
}
