import { useRef, useContext, useEffect, useState } from 'react'
import { GlobalContext } from '../context/global'
import useScript from 'react-script-hook'
import { Redirect } from 'react-router-dom'

const LoginPanel = ({ children }) => <div>{children}</div>
export default ({ clientId } = {}) => {
  const buttonDiv = useRef(null)

  const [oneTap, setOneTap] = useState(true)
  const { user, doGoogleLogin, error } = useContext(GlobalContext)

  const [loading] = useScript({
    src: 'https://accounts.google.com/gsi/client',
  })

  const onSuccess = (response) => {
    doGoogleLogin(response)
  }

  useEffect(() => {
    if (!loading) {
      window.google.accounts.id.initialize({
        client_id:
          '557258334399-4vpeqbtm8iiu0f448sgl7pet75fo54u0.apps.googleusercontent.com',
        callback: onSuccess,
        context: ['signin', 'use'],
        auto_select: true,
        ux_mode: 'popup',
      })

      if (oneTap) {
        try {
          window.google.accounts.id.prompt((notification) => {
            console.log(notification.getNotDisplayedReason())

            if (
              notification.isNotDisplayed() ||
              notification.isSkippedMoment()
            ) {
              console.log('Render')
              setOneTap(notification.isDisplayed())
              window.google.accounts.id.renderButton(buttonDiv.current, {
                theme: 'outline',
                size: 'large',
              })
            }
          })
        } catch (error) {
          console.log('ryr')
          console.error(error)
        }
      }
    }
  }, [loading, oneTap])

  return (
    <div>
      {user?.loggedIn ? (
        <Redirect to="/dashboard" />
      ) : !oneTap ? (
        <LoginPanel>
          <div ref={buttonDiv}></div>
        </LoginPanel>
      ) : (
        <p>Loading...</p>
      )}
    </div>
  )
  // return <div>{user ? <Redirect to="/dashboard" /> : 'Loading'}</div>
}
