import React, { useState, useEffect } from 'react'
import '@fontsource/roboto'
import AccountService from './account/accountservice'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Dashboard from './dashboard/dashboard'
import Home from './home/home'
import Nav from './dashboard/nav'
import About from './dashboard/about'
import Footer from './common/footer'
import Login from './account/login'
import Logout from './account/logout'
import './App.css'
import { GlobalProvider } from './context/global'
import CssBaseline from '@material-ui/core/CssBaseline'
import GoogleLogin from './account/googlelogin'

const App = ({
  apiBaseUri = process.env.REACT_APP_APIBASEURI || '',
  googleClientId = process.env.REACT_APP_GOOGLE_CLIENT_ID,
}) => {
  const userState = useState({ loggedIn: false })

  const { login, signup, getUserProfile, isLoggedIn, getToken, fetchUserData } =
    AccountService({
      apiBaseUri,
      userState,
      auth: process.env.REACT_APP_AUTHURI,
    })

  return (
    <GlobalProvider apiBaseUri={apiBaseUri}>
      <CssBaseline />
      <Nav />
      <Router>
        <Switch>
          <Route exact path="/">
            <Home signup={signup} clientId={googleClientId} />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/logout">
            <Logout />
          </Route>
          <Route path="/dashboard">
            <Dashboard apiBaseUri={apiBaseUri} />
          </Route>
          <Route path="/login">
            <GoogleLogin clientId={googleClientId} />
          </Route>
        </Switch>
      </Router>
      <Footer />
    </GlobalProvider>
  )
}

export default App
