import { Typography, Card } from '@material-ui/core'

const getReceivedTransactions = ({ user, transactions }) =>
  transactions.filter((txn) => txn.receiver === user)
const getSentTransactions = ({ user, transactions }) =>
  transactions.filter((txn) => txn.sender === user)

const calculateBalance = ({ user, transactions }) =>
  user
    ? getReceivedTransactions({ user, transactions }).reduce(
        (balance, x) => balance + x.amount,
        0
      ) -
      getSentTransactions({ user, transactions }).reduce(
        (balance, x) => balance + x.amount,
        0
      )
    : 0

const BalanceSummary = ({ transactions, user }) => (
  <Typography variant="h4" component="h4" color="textSecondary" align="center">
    {user.givenName} your Balance is{' '}
    <span
      className={
        calculateBalance({ transactions, user: user.username }) < 0
          ? 'error'
          : ''
      }
    >
      &#163;
      {Math.abs(
        calculateBalance({ transactions, user: user.username }).toFixed(2)
      )}{' '}
      {calculateBalance({ transactions, user: user.username }) < 0 ? 'DR' : ''}
    </span>
  </Typography>
)
export { BalanceSummary }
